import axios from 'axios';
//axios helps in making htttp requests
const url = 'api/posts/';

class PostService {
    //Get posts
    static getPosts() {
        return new Promise((resolve, reject) => {
            //get data after fetch
            axios.get(url).then(res => {
                const data = res.data;
                resolve(
                    data.map(post => ({
                        //use spread operator and add the dat to each
                        ...post,
                        createdAt: new Date(post.createdAt)
                    }))
                );
            }).catch(err => {
                reject(err);
            })
        });
    }
    //Create post 

    static insertPost(text) {
        return axios.post(url, {
            text
        })
    }

    //Delete post

    static deletePost(id) {
        return axios.delete(`${url}${id}`)
    }
}

export default PostService;